/* src/App.css */

/* Reset margin and padding for the entire body to remove white space */
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

/* Fullscreen background gradient */
.App {
  font-family: Arial, sans-serif;
  color: #333;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(135deg, #f0f0f0, #d0e1ff);
  animation: gradient 10s ease infinite;
}

/* Profile Section */
.App-header {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  opacity: 0;
  animation: fadeIn 1.5s ease forwards;
}

.profile-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}

.profile-text h1 {
  font-size: 18px;
  color: #888;
  margin: 0;
}

.profile-text h2 {
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

/* About Statement */
.about-statement {
  margin: 40px 0;
  opacity: 0;
  animation: fadeIn 1.5s ease forwards;
  animation-delay: 0.5s;
  text-align: center;
}

.about-statement h3 {
  font-size: 24px;
  color: #333;
  margin-bottom: 10px;
  opacity: 0;
  transform: translateX(-100px);
  animation: slideIn 1.5s ease forwards;
  animation-delay: 1s;
}

@keyframes slideIn {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Projects Section */
.projects-section {
  width: 100%;
  padding: 20px;
  max-width: 1000px;
  opacity: 0;
  animation: fadeIn 1.5s ease forwards;
  animation-delay: 1s;
}

.projects-section h2 {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333;
  text-align: center;
}

/* Remove hyperlink underline and add white background to project cards */
.project-link {
  text-decoration: none; /* Remove underline */
  color: inherit;
  display: block;
}

.project-card {
  display: flex;
  align-items: center;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 20px;
  background-color: #fff; /* Set white background */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.project-image {
  width: 150px;
  height: auto;
  margin-right: 20px;
  border-radius: 8px;
}

.project-info h3 {
  font-size: 22px;
  margin: 0;
}

.project-info p {
  font-size: 14px;
  color: #666;
}
